<template>
  <div>
    <!-- 搜索结果页-头部导航 -->
    <div class="search-result-container">
      <!-- 点击实现后退效果 -->
      <van-nav-bar title="搜索结果" left-arrow @click-left="$router.go(-1)" fixed />
    </div>

    <div>
      <!-- 上拉加载更多组件 -->
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad" :immediate-check="false"
        offset="50">
        <!-- 文章列表 -->
        <ArticleItem v-for="item in articleList" :key="item.art_id" :artObj="item" :SearchParam="false"
          @click.native="itemClickFn(item.art_id)"></ArticleItem>
        <!-- 组件本来是没有点击事件的，.native可以给组件绑定点击事件 -->
      </van-list>
    </div>
  </div>
</template>

<script>
// 搜索结果Api
import { searchResultListAPI } from '@/api/index'

// 导入文章列表的组件
import ArticleItem from '@/components/ArticleItem.vue'

export default {
  name: 'SearchResult',
  components: {
    ArticleItem
  },
  props: ['kw'],
  data() {
    return {
      page: 1,
      articleList: [],
      loading: false,
      finished: false
    }
  },
  methods: {
    // 上拉加载事件方法
    async onLoad() {
      if (this.articleList.length > 0) {
        this.page++
        const { data: res } = await searchResultListAPI({
          page: this.page,
          kw: this.kw
        })
        if (res.data.results.length === 0) {
          this.finished = true
        }
        this.articleList = [...this.articleList, ...res.data.results]
        this.loading = false
      }
    },
    // 点击每个文章组件触发的方法
    itemClickFn(id) {
      this.$router.push({
        // 路径中传入查询参数，把文章id传给服务器
        path: `/detail?art_id=${id}`
      })
    }
  },
  async created() {
    const { data: res } = await searchResultListAPI({
      page: this.page,
      kw: this.kw
    })
    this.articleList = res.data.results
  }
}
</script>

   <style lang="less" scoped>
.search-result-container {
  padding-top: 46px;
}
</style>
